import React, { Component } from 'react';
export  class Resume extends Component {
  render() {
  	 let resumeData = this.props.resumeData;
    return (
      <React.Fragment>
         <section id="resume">
        {/* Education
      ----------------------------------------------- */}
        <div className="row education">
          <div className="three columns header-col">
            <h1><span>Education</span></h1>
          </div>
          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">
                <h3>Grad University</h3>
                <p className="info">{resumeData.resumedata1} <span>•</span> <em className="date">April 2016</em></p>
                <p>
                 I have done my Master's from Marist College in Software Development. The University is located in New York.
                 I have done a couple of projects on web development which drew me into working as an intern in the same college.
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>Undergrad</h3>
                <p className="info">Bachelor's in Computer Science. <span>•</span> <em className="date">April 2014</em></p>
                <p>
                  I completed my Bachelor's in Computer Science from ANU, India. It was so glad to study in ANU since there are a lot of friendly staff
                  and we were taught a lot of basics in different technologies. My interest in web development was from then, I started learning and creating basic simple
                  websites, and I have also done an internship in Mumbai. 
                </p>
              </div>
            </div> {/* item end */}
          </div> {/* main-col end */}
        </div> {/* End Education */}
        {/* Work
      ----------------------------------------------- */}
        <div className="row work">
          <div className="three columns header-col">
            <h1><span>Work</span></h1>
          </div>
          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">
                <h3>General Electric</h3>
                <p className="info">Web Developer <span>•</span> <em className="date">Aug 2017 - Present</em></p>
                <p>
                  I'm working as a web developer in GE from Aug which is located in Dayton, OH. It was fun working in the company, and I've 
                  learnt a lot from there. My role is to maintain and create websites for several departments, we use PHP, Javascript, Reactjs, HTML5 and CSS.
                  I have also enhanced user experience by designing new web features using MVC Framework React.js
                </p>
              </div>
            </div> {/* item end */}
            <div className="row item">
              <div className="twelve columns">
                <h3>Wilmington University</h3>
                <p className="info">Web Developer <span>•</span> <em className="date">May 2016 - June 2017</em></p>
                <p>
                  I worked as web developer in Wilmington University in Delaware. I am front end developer and I used to maintain the univeristy 
                  website and developed all UI pages using HTML, CSS, Javascript, PHP and Reactjs. I have also implemented the Drag and Drop functionality using jQuery framework.
                </p>
              </div>
            </div> {/* item end */}
          </div> {/* main-col end */}
        </div> {/* End Work */}
        {/* Skills
      ----------------------------------------------- */}
        <div className="row skill">
          <div className="three columns header-col">
            <h1><span>Skills</span></h1>
          </div>
          <div className="nine columns main-col">
            <p>As I am working as a web developer since 3years, I've a good experience in developing websites. And Im expertise in few technologies
            such as PHP, Reactjs, SQL, Javascript, HTML5, CSS, Automation testing. How much I am good at each technology is shown below.
            </p>
            <div className="bars">
              <ul className="skills">
               <li><span className="bar-expand php" /><em>php</em></li>
                <li><span className="bar-expand css" /><em>CSS</em></li>
                <li><span className="bar-expand html5" /><em>HTML5</em></li>
                <li><span className="bar-expand jquery" /><em>jQuery</em></li>
                <li><span className="bar-expand reactjs" /><em>Reactjs</em></li>
                <li><span className="bar-expand Automationtesting" /><em>Automation testing</em></li>
              </ul>
            </div>{/* end skill-bars */}
          </div> {/* main-col end */}
        </div> {/* End skills */}
      </section>
      </React.Fragment>
    );
  }
}
export default Resume;