import React, { Component } from 'react';

export class About extends Component {
  render() {
     let resumeData = this.props.resumeData;
    return (
     <section id="about">
        <div className="row">
          <div className="three columns">
            <img className="profile-pic" src="images/me.jpg" alt="Smiley face" />
          </div>
          <div className="nine columns main-col">
            <h2>About Me</h2>
            <p>{resumeData.aboutme}
            </p>
            <div className="row">
              <div className="columns contact-details">
                <h2>Contact Details</h2>
                <p className="address">
                  <span>Thusharika Nuthalapati</span><br />
                  <span>9220 worthington Rd<br />
                    Westerville, OH, US
                  </span><br />
                  <span>732-781-8860</span><br />
                  <span>harikan0278@gmail.com</span>
                </p>
              </div>
              
            </div> {/* end row */}
          </div> {/* end .main-col */}
        </div>
      </section>
      
    );
  }
}

export default About;
